.seller-section {
    margin-bottom: 120px;
  }
  .seller-section h1 span {
    color: var(--orange);
  }
  .seller-section h1 {
    text-align: center;
    margin-bottom: 50px;
    font-family: Montserrat;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px; /* 116.667% */
    letter-spacing: -0.72px;
}

.seller-section .container {
    padding: 0 33px;
  }
.my-images ul {
    list-style: none;
    display: flex;
    gap: 16px;
    width: 100%;
    height: 100%;
    padding-bottom: 48px;
    justify-content: space-between;
    margin: 0;
    padding-left: 0;
    flex-wrap: wrap;
   
  }
  .caption{
    padding-left: 0;
    
  }
  .pic img {
    border-radius: 15px;
    width: 100%;
  }
.buy-btn{
  border-radius: 100px;
padding: 14px 40px;
width: 162px;
height: 50px;
background: #fd9222;
border: none;
}

.pic{
   width: calc((100% - 32px) / 3 );
}
.caption-mobile{
  display: none;
  color: var(--black);
font-family: Montserrat;
font-size: 14px;
font-style: normal;
font-weight: 400;
line-height: 18px; /* 128.571% */
letter-spacing: -0.28px;
}
