@media screen and (min-width:768px) and (max-width:1158px) {
  .pic:nth-child(3){
    display:none;
  }
  .pic{
    width: calc((100% - 16px) / 2 );}
}
@media screen and (max-width: 767px) {
  .seller-section h1 {
    font-size: 20px;
  }
  .pic{
    width: 100%;
  }
  .caption-mobile{
    display: block;
  }
  .caption{
    display: none;
  }
  .pic:nth-child(3){
    display:none;
  }
  .buy-btn{
    margin: auto;
  }
  .buy-btn-div{
    width: 100%;
    text-align: center;
  }
}

